/**
 *
 *
 * NO Global VARS Allowed except namespace, events, log and error
 * *******************************************
 *
 * Note : for the sake of clarity, maintainability and security
 * 1. keep the same "_" for private api methods functions (e.g : _myMethod),
 * 2. use camelCasing as a standard
 * 3. Don't make public string or array variables, use objects instead to ensure continuous Object oriented programming API.
 * 4. Declare any jQuery selector (or any third party reliant stuff) with a $ (e.g : $myCachedJquerySelectorPublicVariable)
 * 5. Make sure you really need to make anything public, most problems can be solved through eventBinding
 * 6. Never trust user input
 * 7. Assume network (internet) will always fail (because it will)
 * 8. Make sure your modules are contained : if they break, they'll emit an error and leave any other module just fine.
 * 9. Don't ever parse HTML / CSS with regexp. It's not safe if it's complex, if you need it : make minimum use of it.
 * http://stackoverflow.com/questions/1732348/regex-match-open-tags-except-xhtml-self-contained-tags/1732454#1732454
 * 10. Avoid "FOUC", make sure code is Async and based on promise. Use a CSS empty skeleton whenever possible.
 * https://en.wikipedia.org/wiki/Flash_of_unstyled_content
 * 11. Cache what you need and Don't Repeat Yourself (be lazy)
 * 12. Never evaluate sensitive Data such as passwords even crypted on the frontend side. You allow timing attacks.
 * 13. Don't store/track user Data or input just because you can. It's bad for performance and troubles ethics.
 * 14. Don't use "layout trashers". If you do cache them for the sake of performance.
 * https://gist.github.com/paulirish/5d52fb081b3570c81e3a
 *
 */
// APP Namespace
var ividata = ividata || {};

/*****************************************************************************
 * API Core module || Javascript module pattern
 * @IMPORTANT : we use loose coupling module pattern to enhance minification
 * @type {{modules: {}, core: {cache, tools, templates, init}}}
 *****************************************************************************/
ividata = {
    modules: {},
    // Api definition
    core: (function () {

        /**
         * API public vars
         */
        var _activeSizes,
            _mobile = 0,
            _tablet = 0;

        /********************************************
         *______________ API ToolBox________________*
         ********************************************/

        /**
         * Events debouncing
         * @param fun
         * @param mil
         * @returns {Function}
         */
        function debounce(fun, mil){
            var timer;
            return function(){
                clearTimeout(timer);
                timer = setTimeout(function(){
                    fun();
                }, mil);
            };
        }


        /**
         * Define if device is mobile
         * @returns {number}
         * @private
         */
        function _defineMobile() {
            var sizes = _getSizes();
            if(sizes.ww < 768){
                _setMobile(1);
            }
            if(sizes.ww <= 1024){
                _tablet = 1;
            }
            log = ["mobile is : ", _mobile];
            events.emit('_logThis', log);
            return ividata.core.tools.mobile = _mobile;
        }
        function _setMobile(boolean) {
            return _mobile = boolean;
        }

        function _getMobile() {
            return _mobile;
        }

        /**
         * Calculate and publish screen sizes to API & modules
         * @returns {{ww: Number, wh: Number, sw: Number, sh: Number}|*}
         * @private
         */
        function _calcSize() {
            _activeSizes = {
                ww: window.innerWidth,
                wh: window.innerHeight,
                sw: screen.width,
                sh: screen.height,
                dh : document.body.scrollHeight
            };

            log = ["window/screen dimensions :", _activeSizes.ww, _activeSizes.wh, _activeSizes.sw, _activeSizes.sh];
            events.emit('_logThis', log);

            return ividata.core.tools.activeSizes = _activeSizes;

        }
        function _getSizes(){
            _activeSizes = {
                ww: window.innerWidth,
                wh: window.innerHeight,
                sw: screen.width,
                sh: screen.height,
                dh : document.body.scrollHeight
            };
            return _activeSizes;
        }
        function _setSizes(object) {
            return _activeSizes = object;
        }

        /**
         * AutoComplete Override to let mobile user type correctly
         */
        function turnOffAutoComplete() {
            var isWebkit = 'WebkitAppearance' in document.documentElement.style;
            var isChrome = /Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor);
            var isSafari = /Safari/.test(navigator.userAgent) && /Apple Computer/.test(navigator.vendor);

            if(isChrome || isSafari || isWebkit) {
                var inputs = document.querySelectorAll('input');
                var inputsAr = Array.prototype.slice.call(inputs, 0);
                inputsAr.forEach(function(input){
                    input.setAttribute('autocomplete', 'off');
                });
            }
        }

        /**
         * Bug Logging function
         * @param data
         * @private
         */
        function _logThis(data) {
            console.log(data);
        }

        /**
         * Bus error handler
         * @param errorMessage
         * @private
         */
        function _errorGarbageCollector(errorMessage) {
            console.log(errorMessage);
        }

        /***********************************************************************
         *  Templates rendering functions
         * @doc : declare a function for each template that needs specific
         * Javascript code and modules to run.
         ***********************************************************************/
        function _render() {
            _calcSize();
            _defineMobile();
        }

        function landingRender(){
            ividata.modules.backgroundchange.init();
        }


        /*********************************************************************************
         * Asynchronous eventss binders || Javascript Observer (PubSub Pattern)
         * IMPORTANT : BIND any events here that you need to bind so that module can
         * communicate from a single place
         * WARNING : EMIT events from function / controller only, this function
         * is to bind declared events emissions
         *********************************************************************************/
        /**
         * Run events binding for debugging purpose that wont run in production
         * @private
         */
        function _debugEventBinder() {
            // declare your pre-production window.location.hostname from here
            var env = window.location.hostname;
            var pattern1 = /localhost/;
            var pattern2 = /ividata.app/;
            if (pattern1.test(env) || pattern2.test(env)) {
                console.log('%c Debug Mode... ', 'color : #bada55; font-weight:bolder;');

                // events Logger for debugging purpose only
                events.on('_logThis', _logThis);
                // error printer do not use in production mode
                events.on('error', _errorGarbageCollector);

            }
            else {
                console.log('%c Production Mode... ', 'color : #bada55; font-weight:bolder;');
            }
        }

        /**
         * Bind events across modules from here
         * @private
         */
        function _eventBinder() {


        }

        /*********************************************************************************
         *_________________________ Application core init function_______________________*
         *********************************************************************************/
        function init() {
            _debugEventBinder();
            _eventBinder();

            log = 'Core init';
            events.emit('_logThis', log);

            _render();
        }


        /********************************************************************************
         * Public API methods / vars declaration || Javascript revealing module pattern
         * Use these from Templates to call js modules through public core API functions
         * Publish usefull data to all modules from here too
         ********************************************************************************/

        return {
            tools : {
                _isMobile : _getMobile,
                _setMobile : _setMobile,
                _calcSize : _calcSize,
                _setSize : _setSizes,
                _getSize : _getSizes,
                turnOffAutoComplete: turnOffAutoComplete,
                debounce : debounce
            },
            templates : {
                landing: landingRender
            },
            init: init
        };
    })()
};


