(function($) {
    $(document).ready(function() {

      var MMD_window_W = $(window).width();
      var steakPlayer = $('.imageplayer');
      var backLocation = document.location.hostname;

      function BackPreLoad() {

        if(MMD_window_W < 767){
          format = 'mobile/';
        } else if (MMD_window_W < 769) {
          format = 'desktop/';
        } else {
          format = 'desktop/';
        }

        if (backLocation.indexOf("localhost") !== -1) {
          path = 'images/';
        } else if (backLocation.indexOf("alexandre") !== -1) {
          path = 'images/';
        } else if (backLocation.indexOf("hardi") !== -1) {
          path = 'images/';
        } else {
          path = '/assets/custom/biglari/player/images/';
        }

        i = 0;
        while(i < 240){
          if(i<10){
            steakPlayer.append('<img src="' + path + 'sequence/' + format + 'maquette_riva_0000'+ i +'.jpg">');
          } else if(i<100) {
            steakPlayer.append('<img src="' + path + 'sequence/' + format + 'maquette_riva_000'+ i +'.jpg">');
          } else {
            steakPlayer.append('<img src="' + path + 'sequence/' + format + 'maquette_riva_00'+ i +'.jpg">');
          }
          i++;
        }//endwhile
      }

      BackPreLoad();

      $('#imageplayer').imgplay({
        rate: 24,
        controls: true,
        loop: true,
        cover: true
      });

      function playBack() {
        $('#imageplayer').data('imgplay').play();
      }

      playBack();

  });
})(jQuery);
